'use strict';

/**
 * Represents a rectangle
 *
 * @class
 */
var Rectangle = function () {
    /**
     * Left
     *
     * @property left
     * @type Integer
     */
    this.left = 1000;

    /**
     * Right
     *
     * @property right
     * @type Integer
     */
    this.right = -1000;

    /**
     * Top
     *
     * @property top
     * @type Integer
     */
    this.top = 1000;

    /**
     * Bottom
     *
     * @property bottom
     * @type Integer
     */
    this.bottom = -1000;
};

module.exports = Rectangle;
